.container {
  padding: 0 3rem; }

.inline-v-align {
  width: 90%;
  vertical-align: middle;
  display: inline-block; }

@font-face {
  font-family: 'Icons';
  src: url("../fonts/icon-font.eot"), url("../fonts/icon-font.woff") format("woff"), url("../fonts/icon-font.ttf") format("truetype"); }

[class^="icon"]:after {
  font-family: 'Icons', sans-serif;
  font-size: 1em;
  display: inline-block;
  vertical-align: middle; }

.icon-facebook:after {
  content: '\EA01'; }

.icon-instagram:after {
  content: '\EA02'; }

.icon-linkedin:after {
  content: '\EA03'; }

.icon-twitter:after {
  content: '\EA04'; }

.icon-youtube:after {
  content: '\EA05'; }

.icon-vimeo:after {
  content: '\EA06'; }

.icon-behance:after {
  content: '\EA07'; }

.icon-flickr:after {
  content: '\EA08'; }

.icon-dribbble:after {
  content: '\EA09'; }

.icon-imdb:after {
  content: '\EA10'; }

/*==============================================================================
# FONTS
==============================================================================*/
@font-face {
  font-family: 'Aileron';
  src: url("../fonts/aileron/aileron-bold-webfont.woff2") format("woff2"), url("../fonts/aileron/aileron-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: 'Aileron';
  src: url("../fonts/aileron/aileron-semibold-webfont.woff2") format("woff2"), url("../fonts/aileron/aileron-semibold-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'Aileron';
  src: url("../fonts/aileron/aileron-light-webfont.woff2") format("woff2"), url("../fonts/aileron/aileron-light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

h1, h2, h3, h4, h5, h6,
p,
ol,
ul,
li,
span {
  font-family: inherit;
  margin: 0;
  padding: 0;
  line-height: 1.5; }

i {
  font-style: normal; }

a {
  text-decoration: none;
  display: inline-block; }

a.fancy-link {
  opacity: 0.3;
  color: #FFFFFF;
  padding: 0.5em 0;
  position: relative;
  transition: opacity 0.4s; }
  a.fancy-link:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 0;
    margin: auto;
    height: 1px;
    background-color: #FFFFFF;
    transition: width 0.4s; }
  a.fancy-link:hover {
    opacity: 1; }
    a.fancy-link:hover:after {
      width: 100%; }

.primary-heading-text {
  display: block;
  margin-bottom: 0.3em;
  letter-spacing: 2.5vw; }
  @media screen and (max-width: 599px) {
    .primary-heading-text {
      letter-spacing: 1.34rem; } }

.sub-heading {
  letter-spacing: 0.175em;
  font-size: 0.55em;
  font-weight: 500; }
  @media screen and (min-width: 600px) {
    .sub-heading {
      letter-spacing: 0.345em; } }
  @media screen and (max-width: 599px) {
    .sub-heading {
      font-size: .44em; } }
  .sub-heading + .sub-heading {
    font-size: 0.35em; }
    @media screen and (min-width: 600px) {
      .sub-heading + .sub-heading {
        letter-spacing: 0.56em; } }

.site-title {
  margin-bottom: 3rem;
  transition: opacity 1s 0.3s, transform 1s 0.3s; }
  .js .site-title {
    opacity: 0;
    transform: translate(0, 2rem); }
  .js .loaded .site-title {
    opacity: 1;
    transform: translate(0, 0); }

h1, .h1 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em;
  font-size: 2.5rem; }
  h1 span, .h1 span {
    display: block; }

h2, .h2 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em;
  font-size: 1.4rem;
  margin: 0.5em 0; }

.home h2, .home .h2 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em;
  margin: 3em 0;
  transition: opacity 2s; }
  .js .home h2, .js .home .h2 {
    opacity: 0; }
  .js .home h2.seen, .js .home .h2.seen {
    opacity: 1; }

h3, .h3 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em; }

h4, .h4 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em; }

h5, .h5 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em; }

h6, .h6 {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin-bottom: 1.5em; }

.paragraph, .paragraph-lg, .paragraph-sm {
  line-height: 1.75;
  letter-spacing: 0.1em;
  font-size: 1.125rem;
  margin-bottom: 1.5em;
  text-align: left; }

.paragraph-lg {
  font-size: 1.125rem; }

.paragraph-sm {
  font-size: 0.8rem; }

* {
  box-sizing: border-box; }

html {
  overflow-x: hidden; }
  @media screen and (max-height: 768px) {
    html {
      font-size: 95%; } }
  @media screen and (max-width: 599px) {
    html {
      font-size: 80%; } }

html, body {
  background-color: #1d1d1d;
  color: #f3f3f3;
  width: 100%;
  font-family: 'Aileron', sans-serif;
  font-weight: 300; }

body {
  position: relative;
  min-height: 100vh; }
  @media screen and (max-width: 1000px) {
    body.hide-overflow {
      overflow: hidden; } }
  @media screen and (max-width: 699px) {
    body.show-mobile-menu {
      overflow: hidden; } }

a {
  color: inherit; }

::selection {
  background: rgba(213, 213, 217, 0.05); }

/*==============================================================================
# LOGIN
==============================================================================*/
body.login h1 a {
  background-image: url("../images/horizon-logo-final.png");
  background-size: contain;
  width: 100%;
  height: 275px;
  max-height: 20vh;
  max-width: 90vw;
  opacity: .7;
  transition: opacity .2s; }
  body.login h1 a:hover {
    opacity: 1; }

body.login div#login {
  width: 600px;
  max-width: 90vw; }

body.login div#login form#loginform {
  background: none;
  box-shadow: none; }

body.login div#login form#loginform p label {
  font-size: .8rem;
  color: #FFFFFF;
  text-transform: uppercase;
  letter-spacing: .2rem; }

body.login div#login form#loginform input#user_login,
body.login div#login form#loginform input#user_pass {
  padding: 0 2rem;
  height: 60px;
  line-height: 60px;
  font-weight: 300;
  margin: .5rem 0 3rem;
  opacity: .85;
  transition: opacity .2s; }
  body.login div#login form#loginform input#user_login:hover, body.login div#login form#loginform input#user_login:focus,
  body.login div#login form#loginform input#user_pass:hover,
  body.login div#login form#loginform input#user_pass:focus {
    opacity: 1; }
  body.login div#login form#loginform input#user_login::selection,
  body.login div#login form#loginform input#user_pass::selection {
    background: #1d1d1d;
    color: #d5d5d9; }

body.login div#login form#loginform p.submit input#wp-submit {
  background: none;
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  text-transform: uppercase;
  letter-spacing: .2rem;
  box-shadow: none;
  text-shadow: none;
  border-radius: 0;
  padding: 0 4rem;
  height: 60px;
  line-height: 60px;
  opacity: .5;
  transition: all .2s; }
  body.login div#login form#loginform p.submit input#wp-submit:hover {
    opacity: 1; }

body.login div#login form#loginform p.forgetmenot {
  height: 60px;
  line-height: 60px;
  opacity: .5;
  transition: all .2s; }
  body.login div#login form#loginform p.forgetmenot:hover {
    opacity: 1; }

body.login div#login p#backtoblog a,
body.login div#login p#nav a {
  color: #FFFFFF;
  opacity: .3;
  transition: all .2s; }
  body.login div#login p#backtoblog a:hover,
  body.login div#login p#nav a:hover {
    opacity: 1; }
